.hero {
  padding-top: 1.875rem;
  background-color: #495e57;
  margin-top: 100px;
}
.hero-head {
  font-family: "Markazi Text";
  font-weight: 400;
  line-height: 1;
}
.hero .container {
  grid-template-columns: 1fr 1fr;
  gap: 3.5rem;
  margin-bottom: 9.375rem;
}

.hero-information {
  grid-column: 1;
}

.hero-information h1 {
  color: #f4ce14;
}

.hero-information h2 {
  color: #ffffff;
}

.hero-information p {
  margin-top: 1.5rem;
  color: #ffffff;
}

.hero-information .button-primary {
  display: inline-block;
  margin-top: 2rem;
}

.hero-information .button-primary:hover,
.hero-information .button-primary:focus {
  border-color: #f4ce14;
}

.hero-image {
  grid-column: 2;
  width: 100%;
  height: 24rem;
  margin-bottom: -4.375rem;
  justify-self: end;
  border-radius: 1rem;
  object-fit: cover;
}

/*-- Media queries ------------------------------------------------------*/

@media (max-width: 792px) {
  .hero .container {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto;
  }
  .hero {
    margin-top: 80px;
  }
  .hero-information {
    grid-row: 1;
  }

  .hero-image {
    grid-column: 1;
    grid-row: 2;
  }
}
