.navbar {
  padding: 20px 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar .logo {
  width: 50px;
}
.nav-logo {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}
.nav-item a {
  padding: 8px;
  text-decoration: none;
  font-size: 1.3rem;
  font-weight: 550;
  color: #495e57;
}
.navbar-nav .nav-link:hover {
  color: #23e9a4;
}
.navbar-nav {
  margin-left: auto;
}
/* .nav-text {
    padding: 0px 20px;
  } */
.nav-item {
  margin-left: 15px;
}
.navbar-nav .nav-link:visited {
  color: white;
}
.modal-header .btn-close {
  background-image: url(../assets/close.png);
}
.active {
  padding-bottom: 2px;
  border-bottom: 5px solid #f4ce14;
}
.navbar {
  transition: background-color 0.3s ease;
}

.navbar.scrolled {
  background-color: white;
}

@media only screen and (max-width: 600px) {
  .navbar-nav .nav-item {
    padding: 0px 0px;
    text-align: center;
    font-size: 0.9rem;
    margin-bottom: 25px;
  }
  .nav-item {
    margin-bottom: 6px;
    margin-left: 0px;
  }
  .navbar-toggler-icon {
    background-image: url(../assets/toggler-image.png);
  }
  .nav-btn {
    margin-right: 30px;
  }
  .navbar {
    width: 100%;
  }
}
